<template>
  <div class="loginContainer">
    <head-top :head-title="loginWay? '':''" :goBack=true></head-top>
    <form class="loginForm" v-if="loginWay">
      <section class="input_container phone_number">
        <yd-cell-item>
          <span slot="left"></span>
          <yd-input slot="right" required  name="phone" v-model="phoneNumber" max="11" placeholder=""></yd-input>
        </yd-cell-item>
       <!-- <input type="text" placeholder="" name="phone" maxlength="11" v-model="phoneNumber">-->
        <button @click.prevent="getVerifyCode" :class="{right_phone_number:rightPhoneNumber}" v-show="!computedTime"></button>
        <button  @click.prevent v-show="computedTime">({{computedTime}}s)</button>
      </section>
      <section class="input_container">
        <input type="text" placeholder="" name="mobileCode" maxlength="6" v-model="mobileCode">
      </section>
    </form>
    <form class="loginForm" v-else>
      <section class="1input_container">
        <yd-cell-group>
          <yd-cell-item>
 <!--           <span slot="left">:</span>-->
            <yd-input slot="right" required  v-model="userAccount" max="11" placeholder=""></yd-input>
          </yd-cell-item>
          <yd-cell-item>
           <!-- <span slot="left"></span>-->
            <yd-input slot="right" type="password" v-model="passWord" placeholder=""></yd-input>
          </yd-cell-item>
          <yd-cell-item>
            <span slot="left"> <input type="text" placeholder="" maxlength="4" v-model="codeNumber"></span>
            <span slot="right">
                      <div class="img_change_img">
                      <img v-show="captchaCodeImg" :src="captchaCodeImg">
                      <div class="change_img" @click="getCaptchaCode">
                        <p></p>
                        <p></p>
                      </div>
                    </div>
            </span>
          </yd-cell-item>
        </yd-cell-group>
<!--        &lt;!&ndash;.lazy -  input  change &ndash;&gt;
        <input type="text" placeholder="" v-model.lazy="userAccount">-->

      </section>
<!--      <section class="input_container">
        <input type="password" v-if="!showPassword" placeholder="" v-model="passWord">
        <input v-else type="text" placeholder=""  v-model="passWord">
        <div class="button_switch" >
          <yd-switch v-model="showPassword"></yd-switch>
        </div>
      </section>-->
 <!--     <section class="input_container">
        <input type="text" placeholder="" maxlength="4" v-model="codeNumber">
        <div class="img_change_img">
          <img v-show="captchaCodeImg" :src="captchaCodeImg">
          <div class="change_img" @click="getCaptchaCode">
            <p></p>
            <p></p>
          </div>
        </div>
      </section>-->

    </form>
    <p class="login_tips">
      
    </p>
    <p class="login_tips">
      
    </p>

    <div class="login_container">
      <yd-button size="large" type="primary" @click.native="mobileLogin"></yd-button>
    </div>
    <div class="login_container">
      <router-link  to="/forget"v-if="!loginWay">
        <yd-button size="large" type="hollow"></yd-button>
      </router-link>
    </div>

  </div>
</template>

<script type="text/ecmascript-6">
  import headTop from 'components/header/head'
  import {mobileCode, checkExsis, sendLogin, getcaptchas, accountLogin} from '../../service/getData'
  import {mapMutations} from 'vuex'
  export default {
    data(){
      return {
        loginWay: false, //
        showPassword: false, // 
        phoneNumber: null, //
        mobileCode: null, //
        validate_token: null, //
        computedTime: 0, //
        userInfo: null, //
        userAccount: null, //
        passWord: null, //
        captchaCodeImg: null, //
        codeNumber: null, //
        showAlert: false, //
        alertText: null, //
      }
    },
    methods:{
      //
      //https://segmentfault.com/a/1190000007535316
      //async  function , Promise 
      async getVerifyCode(){
        if (this.rightPhoneNumber) {
          this.computedTime = 30;
          this.timer = setInterval(() => {
            this.computedTime --;
            if (this.computedTime == 0) {
              clearInterval(this.timer)
            }
          }, 1000)
          //
          let exsis = await checkExsis(this.phoneNumber, 'mobile');
          if (exsis.message) {
            this.$dialog.alert({mes:exsis.message});
            return
          }else if(!exsis.is_exists) {
            this.$dialog.alert({mes:''});
            return
          }
          //
          let res = await mobileCode(this.phoneNumber);
          if (res.message) {
            this.$dialog.alert({mes: res.message});
            return
          }
          this.validate_token = res.validate_token;
      }
    },
      //
      async getCaptchaCode(){
        let res = await getcaptchas();
        this.captchaCodeImg = res.code;
      },
      //
      async mobileLogin(){
        if (this.loginWay) {
          if (!this.rightPhoneNumber) {
            this.$dialog.alert({mes: ''});
            return
          }else if(!(/^\d{6}$/gi.test(this.mobileCode))){
            this.$dialog.alert({mes: ''});
            return
          }
          //
          this.userInfo = await sendLogin(this.mobileCode, this.phoneNumber, this.validate_token);
        }else{
          if (!this.userAccount) {
            this.$dialog.alert({mes: '//'});
            return
          }else if(!this.passWord){
            this.$dialog.alert({mes: ''});
            return
          }else if(!this.codeNumber){
            this.$dialog.alert({mes: ''});
            return
          }
          //
          this.userInfo = await accountLogin(this.userAccount, this.passWord, this.codeNumber);
        }
        //
        if (!this.userInfo.user_id) {
          this.$dialog.alert({mes: this.userInfo.message});
          if (!this.loginWay) this.getCaptchaCode();
        }else{
          this.RECORD_USERINFO(this.userInfo);
          this.$dialog.notify({
            mes: '',
            timeout: 1000,
            callback: () => {
              this.$router.go(-1);
            }
          });
        }
      },
      ...mapMutations([
        'RECORD_USERINFO'
      ])

   },
    created(){
      this.getCaptchaCode();
    },
    computed:{
        rightPhoneNumber:function () {
          return /^1\d{10}$/gi.test(this.phoneNumber)
        }
      },
   components:{
    headTop
  }
  }
</script>
<style lang="scss" scoped>
  @import '../../style/mixin';
  .loginContainer{
    padding-top: 1.95rem;
    p, span, input{
      font-family: Helvetica Neue,Tahoma,Arial;
    }
  }
  .change_login{
    position: absolute;
    @include ct;
    right: 0.75rem;
    @include sc(.7rem, #fff);
  }

  .loginForm{
    background-color: #fff;
    margin-top: .6rem;
    .input_container{
      display: flex;
      justify-content: space-between;
      padding: .6rem .8rem;
      border-bottom: 1px solid #f1f1f1;
      input{
        flex: 1;
        padding-right: 0.5rem;
        height: 32px;
        line-height: 32px;
      }
      button{
        @include sc(.65rem, #fff);
        font-family: Helvetica Neue,Tahoma,Arial;
        padding: .28rem .4rem;
        border: 1px;
        border-radius: 0.15rem;
      }
      .right_phone_number{
        background-color: #4cd964;
      }
    }
    .phone_number{
      padding: .3rem .8rem;
    }
    .img_change_img{
      display: flex;
      /*flex: 0 0 100px;
      width: 100px;*/
      align-items: center;
      img{
        @include wh(3.5rem, 1.5rem);
        margin-right: .2rem;
      }
      .change_img{
        display: flex;
        flex-direction: column;
        flex-wrap: wrap;
        width: 3rem;
        justify-content: center;
        p{
          @include sc(.55rem, #666);
        }
        p:nth-of-type(2){
          color: #3b95e9;
          margin-top: .2rem;
        }
      }
    }
  }
  .login_tips{
    @include sc(.5rem, red);
    padding: .4rem .6rem;
    line-height: .5rem;
    a{
      color: #3b95e9;
    }
  }
  .login_container{
    margin: 0 .5rem 1rem;
    height: 52px;
    line-height: 52px;
    padding: .5rem 0;
  }
  .button_switch{

  }
  .change_to_text{
    background-color: #4cd964;
  }
  .to_forget{
    float: right;
    @include sc(.6rem, #3b95e9);
    margin-right: .3rem;
  }
</style>



